
@font-face {
  font-family: 'SF';
  src: url('../fonts/SFProDisplay-Bold.otf')  format('opentype'),
       url('../fonts/SFProDisplay-Bold.eot') format('embedded-opentype'),
       url('../fonts/SFProDisplay-Bold.woff') format('woff'), 
       url('../fonts/SFProDisplay-Bold.ttf')  format('truetype'), 
       url('../fonts/SFProDisplay-Bold.svg#SFProDisplay-Bold') format('svg');
  font-weight: 800;
  font-style: normal;
}
@font-face {
  font-family: 'SF';
  src: url('../fonts/SFProDisplay-Semibold.otf')  format('opentype'),
       url('../fonts/SFProDisplay-Semibold.eot') format('embedded-opentype'),
       url('../fonts/SFProDisplay-Semibold.woff') format('woff'), 
       url('../fonts/SFProDisplay-Semibold.ttf')  format('truetype'), 
       url('../fonts/SFProDisplay-Semibold.svg#SFProDisplay-Semibold') format('svg');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'SF';
  src: url('../fonts/SFProDisplay-Light.otf')  format('opentype'),
       url('../fonts/SFProDisplay-Light.eot') format('embedded-opentype'),
       url('../fonts/SFProDisplay-Light.woff') format('woff'), 
       url('../fonts/SFProDisplay-Light.ttf')  format('truetype'), 
       url('../fonts/SFProDisplay-Light.svg#SFProDisplay-Light') format('svg');
  font-weight: 300;
  font-style: normal;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


.App {
  font-family: 'SF', Impact, sans-serif;
  background: linear-gradient(#112f41, #0f5390);
  height: auto;
  min-height: 100vh;
}

.App-header {
  background-color: #112f41;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


h1 {
  font-weight: 600;
  text-align: center;
  color: #fff;
  font-size: 28px;
  margin: 30px 0 20px;
}
.App-content {
  width: 90%;
  margin: 0 auto;
  height: auto;
}
