.expand {
    h3::after {
        content: url('../arrow.svg');
        transition: all 0.2s ease-out;

    }
    .list_title {
        display: block;
        background: #fff;
        border-radius: 5px;
        margin-bottom: 15px;
        >h3 {
            font-size: 16px;
            padding:0 20px;
            font-weight: 800;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            
        }
        &-open {
            background: #F2B134; 
            >h3::after {
                content: url('../arrow_w.svg');
                transform: rotate(90deg);
            }
        }
        .subexpand {
            border-bottom-right-radius: 5px; 
            border-bottom-left-radius: 5px; 
            background: #fff;
            width: 100%;
            padding: 0 20px;

        }
        .sublist_title {
            font-size: 14px;
            >h3 {
                font-size: 14px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 1px;
                background: #D8D8D8;
            }
            &-open {
                >h3::after {
                    content: url('../arrow.svg');
                    transform: rotate(90deg);
                }
            }
            .subtext {
                padding: 5px 0 10px 0;
            }
        }
    }
}